export default {
	props: ['categoryList'],
	data() {
		return {
			showCategoryIndex: 0, //显示类目
			time : 9
		}
	},
	methods: {
		showCategory(item) {
			//分类切换显示
			this.showCategoryIndex = item.id - 1;

			// uni.showLoading({
			// 	title: '加载中...'
			// });
		},
		get_time(){
			setInterval(()=>{
				this.time--;
				if(this.time < 0){
					this.time = 9;
				}
			},1000)
		}
	},
	onLoad() {
		this.get_time()
	}
}
